 comunes para calcular la disminución del tamaño de los objetos debido a la distancia. Estos elementos son: línea de tierra, que es la parte baja del campo de visión; línea de horizonte, es la línea imaginaria en la que se posa la vista, es horizontal y allí se ubican los llamados puntos de fuga; punto de fuga, es el lugar en el que se ubicarían las líneas paralelas, como cuando se dibujan las vías del ferrocarril y se unen en la distancia a pesar de que sabemos que nunca se intersecan; módulos horizontales son los que miden altura y anchura a escala y los módulos verticales permiten ver la profundidad de lo que se aleja; posición aparente del observador, que es un sitio virtual donde se encuentra el espectador y que determina cómo verá los objetos en perspectiva. ")]),_vm._m(18),_c('p',{staticClass:"mb-5"},[_vm._v("La creación de ambientes, personajes y props tienen una gran importancia en la elaboración de una escena de teatro, cine, televisión o videojuegos, ya que complementan ambientes y dan características especiales a situaciones y personajes.")]),_vm._m(19),_vm._m(20),_vm._m(21),_c('SlyderB',{staticClass:"mb-5",attrs:{"datos":_vm.datosSlyder2}}),_vm._m(22),_vm._m(23),_c('p',{staticClass:"mb-5"},[_vm._v("Los props hacen reconocible la personalidad de los personajes y hasta lo ubican históricamente, como por ejemplo si utiliza una espada romana. Los props son creados por un artista que tiene como misión crear ideas o aspectos sobre algo que no se conocía y sobre algo conocido, diseñando el objeto desde varios ángulos, en detalle, para que los modeladores puedan crear el objeto, siendo muy importante en el diseño de videojuegos, películas o televisión.")])],1)],1)}
